<template>
  <div>
    <comp-header-login/>
    <!--================Home Banner Area =================-->
    <section className="banner_area">
      <div className="banner_inner d-flex align-items-center">
        <div className="container">
          <div className="banner_content text-center">
            <h2>Upload Knowledges</h2>
            <div className="page_link">
              <router-link to="/home">Home</router-link>
              <router-link to="/qa">Upload Knowledges</router-link>
            </div>
          </div>
        </div>
      </div>
    </section>
    <!--================End Home Banner Area =================-->

    <!--    <div class="large-12 medium-12 small-12 cell">-->
    <!--      <label>Files-->
    <!--        <input type="file" name="file" id="file" ref="file" multiple v-on:change="handleFilesUpload()"/>-->
    <!--      </label>-->
    <!--    </div>-->
    <!--    <div class="large-12 medium-12 small-12 cell">-->
    <!--      {{ fileName }}-->
    <!--    </div>-->
    <!--    <br>-->
    <!--    <div class="large-12 medium-12 small-12 cell">-->
    <!--      <button v-on:click="addFiles()">Add Files</button>-->
    <!--    </div>-->
    <!--    <br>-->
    <!--    <div class="large-12 medium-12 small-12 cell">-->
    <!--      <button v-on:click="submitFiles()">Submit</button>-->
    <!--    </div>-->
    <div className="file-upload">
      <button className="file-upload-btn" type="button" onClick="$('.file-upload-input').trigger( 'click' )">Add Image
      </button>

      <div className="image-upload-wrap">
        <input className="file-upload-input" type='file' onChange="readURL(this);" accept="image/*"/>
        <div className="drag-text">
          <h3>Drag and drop a file or select add Image</h3>
        </div>
      </div>
      <div className="file-upload-content">
        <img className="file-upload-image" src="#" alt="your image"/>
        <div className="image-title-wrap">
          <button type="button" onClick="removeUpload()" className="remove-image">Remove <span className="image-title">Uploaded Image</span>
          </button>
        </div>
      </div>
    </div>
    <comp-footer/>
  </div>
</template>

<script>

import CompHeaderLogin from "./CompHeaderLogin";
import CompFooter from "./CompFooter";

export default {

  name: "CompUploadKnowledges",
  components: {
    CompHeaderLogin, CompFooter
  },
  data() {
    return {
      files: '',
      fileName: ''
    }
  },
  /*
    Defines the method used by the component
  */
  methods: {
    /*
      Adds a file
    */
    addFiles() {
      this.$refs.file.click();
    },
    /*
      Submits files to the server
    */
    submitFiles() {
      /*
        Initialize the form data
      */
      let formData = new FormData();
      formData.append('file', this.files)
      /*
        Iteate over any file sent over appending the files
        to the form data.
      */
      // for( var i = 0; i < this.files.length; i++ ){
      //   let file = this.files[i];
      //   formData.append('files[' + i + ']', file);
      // }
      /*
        Make the request to the POST /select-files URL
      */
      const axios = require('axios');
      axios.post('http://localhost:1323/qa',
        formData,
        {
          headers: {
            'Content-Type': 'multipart/form-data',
            'Authorization': 'Bearer ' + this.$session.get("token")
          }
        }
      ).then(function () {
        console.log('SUCCESS!!');
      })
        .catch((er) => {
          console.log(er);
        });
    },
    /*
      Handles the uploading of files
    */
    handleFilesUpload() {
      this.files = this.$refs.file.files[0];
      this.fileName = event.target.files[0].name;
    },
    /*
      Removes a select file the user has uploaded
    */
  }
}
</script>

<style scoped>
body {
  font-family: sans-serif;
  background-color: #eeeeee;
}

.file-upload {
  background-color: #ffffff;
  width: 600px;
  margin: 0 auto;
  padding: 20px;
}

.file-upload-btn {
  width: 100%;
  margin: 0;
  color: #fff;
  background: #1FB264;
  border: none;
  padding: 10px;
  border-radius: 4px;
  border-bottom: 4px solid #15824B;
  transition: all .2s ease;
  outline: none;
  text-transform: uppercase;
  font-weight: 700;
}

.file-upload-btn:hover {
  background: #1AA059;
  color: #ffffff;
  transition: all .2s ease;
  cursor: pointer;
}

.file-upload-btn:active {
  border: 0;
  transition: all .2s ease;
}

.file-upload-content {
  display: none;
  text-align: center;
}

.file-upload-input {
  position: absolute;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  outline: none;
  opacity: 0;
  cursor: pointer;
}

.image-upload-wrap {
  margin-top: 20px;
  border: 4px dashed #1FB264;
  position: relative;
}

.image-dropping,
.image-upload-wrap:hover {
  background-color: #1FB264;
  border: 4px dashed #ffffff;
}

.image-title-wrap {
  padding: 0 15px 15px 15px;
  color: #222;
}

.drag-text {
  text-align: center;
}

.drag-text h3 {
  font-weight: 100;
  text-transform: uppercase;
  color: #15824B;
  padding: 60px 0;
}

.file-upload-image {
  max-height: 200px;
  max-width: 200px;
  margin: auto;
  padding: 20px;
}

.remove-image {
  width: 200px;
  margin: 0;
  color: #fff;
  background: #cd4535;
  border: none;
  padding: 10px;
  border-radius: 4px;
  border-bottom: 4px solid #b02818;
  transition: all .2s ease;
  outline: none;
  text-transform: uppercase;
  font-weight: 700;
}

.remove-image:hover {
  background: #c13b2a;
  color: #ffffff;
  transition: all .2s ease;
  cursor: pointer;
}

.remove-image:active {
  border: 0;
  transition: all .2s ease;
}
</style>



